body{
    width: 100%;
    height: 100vh;
    background-color: #000000;
   }
   
   .navbar{
    width: 90%;
    height: 80px;
    background-color: #1B1B1B;
    display: flex;
    align-items: center;
    color: white;
    justify-content: center;
    margin: auto;
    border-radius: 10px;
   
   }
   
   .nav ul li{
    display: flex; 
    text-decoration: none;
    cursor: pointer;
   }
   
   .nav > ul{
    display: flex;
    gap: 50px;
   }
   
   .nav  ul :hover{
    color: aqua;
   }