.testim{
    width: 100%;
    display: flex;
    /* justify-content: center; */
    flex-wrap: wrap;
    flex-direction: column;
    gap: 25px;
   }
   
   .yozuv{
    display: flex;
    flex-direction: column;
    color: white;
   }
   
   .yozuv1{
    width: 570px;
    height: 48px;
   }
   
   .content{
    display: flex;
    gap: 100px;
    align-items: center;
    justify-content: center;
   flex-wrap: wrap;
   padding-top: 8%;
   }
   
   .card1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 445px;
   height: 212px;
    color: white;
    border-left: 2px solid #484848;
    border-top: 2px solid #484848;
    border-bottom: 2px solid #484848;
    border-radius: 5px;
    margin-bottom: 25px;
    padding: 25px;
    word-spacing: 10px;
    text-align: left;
    text-align: justify;
   }
   
   .card1:hover{
    border: 2px solid aqua;
   }
   
   .pictype img{
    width: 50px;
    height: 50px;
   }
   
   .pictype{
    display: flex;
    align-items: center;
    padding-top: 25px;
    gap: 15px;
   }