.gettype{
    width: 100%;
    display: flex; 
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: left;
    text-align: justify;
    word-spacing: 10px;
    margin-top: 50px;
   }
   
   .contact{
    width: 100%;
    display: flex;
    color: white;
    flex-direction: column;
    text-align: left;
    align-items: center;
    padding-top: 20px;
   }
   
   .contact input{
    width: 350px;
    height: 40px;
    border-radius: 5px;
    padding-left: 20px;
    border: none;
    font-size: 20px;
   }
   
   .input{
    display: flex;
    flex-direction: column;
    gap: 10px;
   }
   
   .mobile{
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-top: 15px;
   }
   
   .messege{
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-top: 15px;
    margin: 20px;
   }
   
   .messages-button{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 20px;
   }
   
   .messages-button span{
    color: aqua;
    cursor: pointer;
    font-size: 1.4rem;
    background-color: #1B1B1B;
   }
   
   .messages-button button{
     background-color: gold ;
     color: black;
     width: 390px;
     height: 60px;
     margin-bottom: 20px;
     border-radius: 5px;
     font-size: 1.2rem;
   }
   
   .messages-button button:hover{
    background-color: #000AFF;
    color: aqua;
   }